      justify-content: center;
}

.contact-item {
  font-size: 20px;
  padding-left: 15px;
  margin: 0;
  padding-top: 7px;
  font-family: "Roboto", sans-serif;
  font-weight: 100;
}

@media (max-width: 768px) {
  .footer-container {
    display: -ms-flexbox;
